// BUTTON SHAPES
// This map is used to generate the different button shapes.
// Edit or add shapes to the list below and recompile.
// Each block contains the (name, border-radius).
// The class is generated using the name: (ex .c-btn-square).
//
$btn-shapes: (
  square: 0,
  box: $global-radius,
  rounded: $global-radius,
  pill: 200px,
  circle: 100%
) !default;



/* Button Shapes
* ======================================== */

@each $btn-shape in $btn-shapes {
  $btn-shape-name: nth($btn-shape, 1);
  $btn-shape-radius: nth($btn-shape, 2);

  .c-btn-#{$btn-shape-name} {
    border-radius: $btn-shape-radius;
  }
}

.c-btn-circle {
  overflow: hidden;
  padding-left: 0;
  padding-right: 0;
  // Make btn width equal to height. line height + padding + left&right border;
  width: unquote('calc(#{1em * $btn-line-height} + #{$btn-default-padding-v * 2} + #{$btn-border-width * 2})');
}

.c-btn-box {
  padding-left: $btn-default-padding-v+.2em;
  padding-right: $btn-default-padding-v+.2em;
}
